@import url("https://fonts.googleapis.com/css2?family=Neucha&display=swap");

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #2f74c0;
  font-family: "neucha", cursive;
}
.heading {
  text-transform: uppercase;
  font-size: 40px;
  margin: 30px 0px;
  color: #fff;
  z-index: 1;
  text-align: center;
}

@media (max-width: 800px) {
  .heading {
    font-size: 35px;
    margin: 15px 0;
  }
}
